  <template>
    <div class="home">
      <Sidebar></Sidebar>
      <div>

        <b-modal id="modal-1" title="Status">
          <div class="col-sm-10">
            <select class="form-control" @input="send(datamain)" v-model="status" name="status">
              <option value="pending">Pending</option>
              <option value="approved">Approved</option>
              <option value="rejected">Rejected</option>
            </select>
          </div>
        </b-modal>
      </div>

      <section class="main_section">
        <div class="main_container">

          <div class="section-2">
            <div class="main_cards">

              <div class="col-xl-12">
                <div class="main_cards">
                  <div>
                    <div class="balasnce">
                      Transactions
                    </div>
                    <div class="price">
                      This is a list of latest transactions.
                    </div>
                  </div>
                  <div class="main_tabs">
                    <button :class="{'active':isActive==0}" @click="isActive=0;changeApi()">All</button>
                    <button :class="{'active':isActive==1}" @click="isActive=1;changeApi1()">Sent</button>
                    <button :class="{'active':isActive==2}" @click="isActive=2;changeApi2()">Received</button>
                    <button :class="{'active':isActive==3}" @click="isActive=3;changeApi3()">  Airdrop</button>
                  </div>
                  <div  class="w-100">

                    <table class="table table-striped">
                      <thead>
                      <tr>
                        <th scope="col">TRANSACTION FROM</th>
                        <th scope="col">TRANSACTION TYPE</th>
                        <th scope="col">DATE & TIME</th>
                        <th scope="col">AMOUNT</th>
                        <th scope="col">STATUS</th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr v-for="item in data" :key="item">
                        <th scope="row">{{item.recipientTgUsername}}</th>
                        <td>{{item.coin}}</td>
                        <td>{{item.amount}}</td>
                        <td>{{item.amount}}</td>
                        <td>
                          <router-link to="/transition">
                            <div v-if="item.status=='in_progress'" class="status" :class="{'in_progress' : item.status=='in_progress'}">{{item.status}}</div>
                            <div v-else class="status" :class="{'cancelled' : item.status=='cancelled'}">{{item.status}}</div>
                          </router-link>

                        </td>
                      </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </section>
    </div>
  </template>
  <script>
  import Sidebar from "@/components/Sidebar.vue";
  import 'vue-slick-carousel/dist/vue-slick-carousel.css'
  // optional style for arrows & dots
  import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
  export default {
    components:{
      Sidebar,
    },
    data(){
      return{
        data:[],
        status:'',
        datamain:'',
        isActive:0
      }
    },
    mounted() {
      this.$http.get('manager/send-transaction/',{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`
        }
      })
      .then(res=>{
        this.data=res.data
      })
      // C
    },

    methods:{
      changeApi(){
        this.$http.get('manager/send-transaction/',{
          headers:{
            Authorization:`Bearer ${localStorage.getItem('token')}`
          }
        })
            .then(res=>{
              this.data=res.data
            })
      },
      changeApi1(){
        this.$http.get('manager/send-transaction/',{
          headers:{
            Authorization:`Bearer ${localStorage.getItem('token')}`
          }
        })
            .then(res=>{
              this.data=res.data
            })
      },
      changeApi2(){
        this.$http.get('/manager/receive-transaction/',{
          headers:{
            Authorization:`Bearer ${localStorage.getItem('token')}`
          }
        })
            .then(res=>{
              this.data=res.data
            })
      },
      changeApi3(){
        this.$http.get('/manager/receive-transaction/',{
          headers:{
            Authorization:`Bearer ${localStorage.getItem('token')}`
          }
        })
            .then(res=>{
              this.data=res.data
            })
      },
      send(item){
        this.isCheck=true
        this.$http.put(`manager/send-transaction/${item}/`,{
              "status":this.status

            },
            {
              headers:{
                Authorization:`Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(()=>{
              this.isCheck=false
              this.$toast(`Success`, {
                timeout: 2000,
                type:'success'
              });
              this.$http.get('manager/send-transaction/',{
                headers:{
                  Authorization:`Bearer ${localStorage.getItem('token')}`
                }
              })
                  .then(res=>{
                    this.data=res.data
                  })
            })
            .catch(error=>{
              this.isCheck=false
              this.$toast(`${error.response.status}`, {
                timeout: 2000,
                type:'error'
              });
            })
      },
      send1(){
        this.$http.post('/receive-transaction/',{
              "coin":this.coin,
              "amount":this.price,
              "recipientTgUsername":this.telegram_username

            },
            {
              headers:{
                Authorization:`Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(res=>{
              this.data=res.data
            })
            .catch(error=>{
              this.isCheck=false
              this.$toast(`${error.response.data}`, {
                timeout: 2000,
                type:'error'
              });
            })
      }
    }
  }
  </script>
